import { request } from 'utils';

const prefix = '/api/finance';

export const getReportTitle = () =>
  request({
    prefix,
    url: '/ReportTitle',
    method: 'GET',
    params: {},
    data: {},
  });

export const updateReportTitle = (data) =>
  request({
    prefix,
    url: `/ReportTitle`,
    data,
    method: 'PUT',
  });


