import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Form, Input, Row, Col, message } from 'antd';
import { BtnSave } from 'components/common/button';
import {
  getReportTitle,
  updateReportTitle,
} from 'services/reportTitle';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    MinistryName: '',
    UniversityName: '',
    Position1: '',
    Position2: '',
    Sign1: '',
    Sign2: '',
    Place: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormReportTitleConfig' })
class ReportTitleConfig extends Component {
  state = {
    initialValue: {
      MinistryName: '',
      UniversityName: '',
      Position1: '',
      Position2: '',
      Sign1: '',
      Sign2: '',
      Place: '',
    },
  };

  componentDidMount = () => {
    this.getData();
  };

  getData = async () => {
    const res = await getReportTitle();
    const formatData = {};
    res.data.items.forEach(item => {
      formatData[`${item.titleKey}`] = item.titleValue;
    });
    const initialValue = handleInitialValue(formatData);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const initialValue = handleInitialValue(values);
      const formatData = Object.keys(initialValue).map(key => ({
        titleKey: key,
        titleValue: initialValue[key],
      }));
      this.saveData(formatData);
    });
  }

  saveData = async values => {
    try {
      await updateReportTitle(values);
      message.success('Lưu thành công');
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thiết lập tiêu đề báo cáo">
        <Row>
          <Col span={24}>
            <Form onSubmit={this.handleSubmit}>
              <Form.Item label="Tiêu đề tên bộ" {...formItemLayout}>
                {getFieldDecorator('MinistryName', {
                  initialValue: initialValue.MinistryName,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input placeHolder='ĐẠI HỌC SÂN KHẤU ĐIỆN ẢNH' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề tên trường" {...formItemLayout}>
                {getFieldDecorator('UniversityName', {
                  initialValue: initialValue.UniversityName,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input placeHolder='PHÒNG TÀI VỤ' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề chức danh 1" {...formItemLayout}>
                {getFieldDecorator('Position1', {
                  initialValue: initialValue.Position1,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input placeHolder='NGƯỜI LẬP BIỂU' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề chức danh 2" {...formItemLayout}>
                {getFieldDecorator('Position2', {
                  initialValue: initialValue.Position2,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input placeHolder='TRƯỞNG PHÒNG TÀI VỤ' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề tên người ký 1" {...formItemLayout}>
                {getFieldDecorator('Sign1', {
                  initialValue: initialValue.Sign1,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input placeHolder='NGUYỄN VĂN A' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề tên người ký 2" {...formItemLayout}>
                {getFieldDecorator('Sign2', {
                  initialValue: initialValue.Sign2,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input placeHolder='NGUYỄN VĂN B' />)}
              </Form.Item>
              <Form.Item label="Tiêu đề nơi ký" {...formItemLayout}>
                {getFieldDecorator('Place', {
                  initialValue: initialValue.Place,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input placeHolder='HCM, ngày 08 tháng 08 năm 2019' />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
              </Form.Item>
            </Form>
          </Col>
        </Row>
      </Card>
    )
  }
}

export default props => (
  <Page {...props}>
    <ReportTitleConfig {...props} />
  </Page>
);


